'use client';
import React, { useEffect, useState } from 'react';
import { usePathname } from 'next/navigation';
import Link from 'next/link';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
import Button from '@mui/material/Button';
import Box from '@mui/material/Box';
import IconButton from '@mui/material/IconButton';
import Drawer from '@mui/material/Drawer';
import List from '@mui/material/List';
import ListItemButton from '@mui/material/ListItemButton';
import ListItemText from '@mui/material/ListItemText';
import MenuIcon from '@mui/icons-material/Menu';
import { motion, AnimatePresence } from 'framer-motion';
import { useTheme } from '@mui/material/styles';

const Header = () => {
  const [activeSection, setActiveSection] = useState('home');
  const [mobileOpen, setMobileOpen] = useState(false);
  const [scrolled, setScrolled] = useState(false);
  const [hoveredNav, setHoveredNav] = useState(null);
  const pathname = usePathname();
  const theme = useTheme();

  // Update scroll state for header animation
  useEffect(() => {
    const handleScroll = () => {
      setScrolled(window.scrollY > 50);
      
      // Existing active section logic
      const sections = ['home', 'about', 'contact'];
      const scrollPosition = window.scrollY + 100;

      for (const section of sections) {
        const element = document.getElementById(section);
        if (element) {
          const offsetTop = element.offsetTop;
          const offsetHeight = element.offsetHeight;

          if (scrollPosition >= offsetTop && scrollPosition < offsetTop + offsetHeight) {
            setActiveSection(section);
            break;
          }
        }
      }
    };

    handleScroll();
    window.addEventListener('scroll', handleScroll);
    return () => window.removeEventListener('scroll', handleScroll);
  }, []);

  // Smooth scroll to section with enhanced animation
  const scrollToSection = (sectionId) => {
    const element = document.getElementById(sectionId);
    if (element) {
      window.scrollTo({
        top: element.offsetTop - 80,
        behavior: 'smooth'
      });
    }
  };

  const handleDrawerToggle = () => {
    setMobileOpen((prev) => !prev);
  };

  const isActive = (section) => {
    if (pathname === '/') {
      return activeSection === section;
    }

    return (section === 'home' && pathname === '/') ||
           (section !== 'home' && pathname.includes(section));
  };

  // Animation variants
  const headerVariants = {
    initial: { y: 0, opacity: 1 }, // FIXED: y: -100 se y: 0 kiya
    animate: { 
      y: 0, 
      opacity: 1,
      transition: {
        type: "spring",
        stiffness: 100,
        damping: 20,
        delay: 0.1
      }
    }
  };

  const navItemVariants = {
    initial: { opacity: 0, y: -10 }, // Reduced y displacement
    animate: (i) => ({
      opacity: 1,
      y: 0,
      transition: {
        delay: i * 0.1 + 0.3,
        type: "spring",
        stiffness: 150,
        damping: 15
      }
    }),
    hover: {
      scale: 1.05,
      transition: {
        type: "spring",
        stiffness: 400,
        damping: 10
      }
    }
  };

  const underlineVariants = {
    initial: { scaleX: 0 },
    hover: { scaleX: 1 },
    active: { scaleX: 1 }
  };

  const logoVariants = {
    initial: { opacity: 1, scale: 1 }, // FIXED: opacity 0 se 1 kiya
    animate: { 
      opacity: 1, 
      scale: 1,
      transition: {
        type: "spring",
        stiffness: 200,
        damping: 15,
        delay: 0
      }
    },
    hover: {
      scale: 1.05,
      rotate: [0, -5, 5, -3, 3, 0],
      transition: {
        duration: 0.8,
        ease: "easeInOut"
      }
    }
  };

  const menuIconVariants = {
    initial: { rotate: 0 },
    animate: { rotate: mobileOpen ? 90 : 0 },
    hover: { scale: 1.1 }
  };

  const drawerVariants = {
    closed: {
      x: "100%",
      transition: {
        type: "spring",
        stiffness: 300,
        damping: 30
      }
    },
    open: {
      x: 0,
      transition: {
        type: "spring",
        stiffness: 300,
        damping: 30
      }
    }
  };

  const listItemVariants = {
    closed: { opacity: 0, x: 20 },
    open: (i) => ({
      opacity: 1,
      x: 0,
      transition: {
        delay: i * 0.1,
        type: "spring",
        stiffness: 200,
        damping: 20
      }
    })
  };

  const pulseAnimation = {
    animate: {
      boxShadow: [
        '0px 2px 4px rgba(0,0,0,0.2)',
        '0px 2px 12px rgba(255,255,255,0.1)',
        '0px 2px 4px rgba(0,0,0,0.2)'
      ],
      transition: {
        duration: 2,
        repeat: Infinity,
        ease: "easeInOut"
      }
    }
  };

  return (
    <motion.div
      initial="initial"
      animate="animate"
      variants={headerVariants}
      transition={{ ease: "easeInOut" }}
    >
      <Box 
        component="header"
        sx={{ 
          backgroundColor: 'primary.main', // FIXED: Color change remove kiya
          position: 'fixed',
          width: '100%',
          top: 0,
          zIndex: 1300,
          boxShadow: '0px 2px 4px rgba(0,0,0,0.2)',
          transition: 'all 0.3s ease',
         
          borderBottom: scrolled ? '1px solid rgba(255,255,255,0.1)' : 'none'
        }}
      >
        <Toolbar>
          <motion.div
            variants={logoVariants}
            whileHover="hover"
            style={{ flexGrow: 1 }}
          >
            <Typography 
              variant="h6" 
              component="div" 
              sx={{ 
                color: 'white',
                cursor: 'pointer',
                display: 'inline-block',
                fontWeight: 700,
                fontSize: '1.25rem',
                letterSpacing: '0.5px'
              }}
              onClick={() => scrollToSection('home')}
            >
              Logo
              {/* Animated dot after logo */}
              <motion.span
                animate={{
                  opacity: [0.5, 1, 0.5],
                  scale: [1, 1.2, 1]
                }}
                transition={{
                  duration: 2,
                  repeat: Infinity,
                  ease: "easeInOut"
                }}
                style={{
                  display: 'inline-block',
                  width: '8px',
                  height: '8px',
                  borderRadius: '50%',
                  backgroundColor: 'white',
                  marginLeft: '8px',
                  verticalAlign: 'middle'
                }}
              />
            </Typography>
          </motion.div>
          
          <Box sx={{ 
            display: { 
              xs: 'none',
              sm: 'flex' 
            }, 
            gap: 2, 
            mr: 2 
          }}>
            {['home', 'about', 'contact'].map((section, index) => (
              <motion.div
                key={section}
                custom={index}
                variants={navItemVariants}
                initial="initial"
                animate="animate"
                whileHover="hover"
                onHoverStart={() => setHoveredNav(section)}
                onHoverEnd={() => setHoveredNav(null)}
              >
                <Button 
                  onClick={() => scrollToSection(section)}
                  onMouseEnter={() => setHoveredNav(section)}
                  onMouseLeave={() => setHoveredNav(null)}
                  sx={{ 
                    color: 'white',
                    fontSize: {
                      xs: '0.875rem',
                      sm: '1rem'
                    },
                    position: 'relative',
                    padding: '8px 16px',
                    minWidth: '80px',
                    borderRadius: '8px',
                    overflow: 'hidden',
                    fontWeight: isActive(section) ? 700 : 500,
                    '&:hover': {
                      backgroundColor: 'rgba(255, 255, 255, 0.15)',
                    }
                  }}
                >
                  {section.charAt(0).toUpperCase() + section.slice(1)}
                  
                  {/* Animated background on hover */}
                  <motion.div
                    initial={{ scale: 0 }}
                    animate={hoveredNav === section ? { scale: 1 } : { scale: 0 }}
                    transition={{ duration: 0.3 }}
                    style={{
                      position: 'absolute',
                      top: 0,
                      left: 0,
                      right: 0,
                      bottom: 0,
                      backgroundColor: 'rgba(255, 255, 255, 0.1)',
                      borderRadius: '8px',
                      zIndex: -1
                    }}
                  />
                  
                  {/* Animated underline */}
                  <motion.div
                    variants={underlineVariants}
                    initial="initial"
                    animate={isActive(section) ? "active" : hoveredNav === section ? "hover" : "initial"}
                    style={{
                      position: 'absolute',
                      bottom: '0',
                      left: '10%',
                      width: '80%',
                      height: '2px',
                      backgroundColor: 'white',
                      borderRadius: '1px',
                      transformOrigin: 'left',
                    }}
                  />
                  
                  {/* Floating particles on hover */}
                  <AnimatePresence>
                    {hoveredNav === section && (
                      <>
                        {[...Array(3)].map((_, i) => (
                          <motion.div
                            key={i}
                            initial={{ 
                              opacity: 0, 
                              y: 0, 
                              x: 0 
                            }}
                            animate={{ 
                              opacity: [0, 0.8, 0],
                              y: -20,
                              x: i % 2 === 0 ? -10 : 10
                            }}
                            exit={{ opacity: 0 }}
                            transition={{
                              duration: 0.6,
                              delay: i * 0.1
                            }}
                            style={{
                              position: 'absolute',
                              width: '4px',
                              height: '4px',
                              borderRadius: '50%',
                              backgroundColor: 'white',
                              top: '50%',
                              left: '50%'
                            }}
                          />
                        ))}
                      </>
                    )}
                  </AnimatePresence>
                </Button>
              </motion.div>
            ))}
          </Box>

          {/* Mobile Menu */}
          <Box sx={{ display: { xs: 'flex', sm: 'none' } }}>
            <motion.div
              variants={menuIconVariants}
              animate="animate"
              whileHover="hover"
            >
              <IconButton
                onClick={handleDrawerToggle}
                sx={{ 
                  color: 'white',
                  position: 'relative',
                  overflow: 'hidden'
                }}
                aria-label="open navigation"
                size="large"
              >
                {/* Ripple effect on click */}
                <motion.div
                  animate={mobileOpen ? pulseAnimation.animate : {}}
                  style={{
                    position: 'absolute',
                    top: 0,
                    left: 0,
                    right: 0,
                    bottom: 0,
                    borderRadius: '50%',
                    backgroundColor: 'rgba(255,255,255,0.1)'
                  }}
                />
                <MenuIcon />
              </IconButton>
            </motion.div>
            
            <AnimatePresence>
              {mobileOpen && (
                <motion.div
                  initial={{ opacity: 0 }}
                  animate={{ opacity: 1 }}
                  exit={{ opacity: 0 }}
                  style={{
                    position: 'fixed',
                    top: 0,
                    left: 0,
                    right: 0,
                    bottom: 0,
                    backgroundColor: 'rgba(0,0,0,0.5)',
                    zIndex: 1299
                  }}
                  onClick={handleDrawerToggle}
                />
              )}
            </AnimatePresence>

            <motion.div
              initial="closed"
              animate={mobileOpen ? "open" : "closed"}
              variants={drawerVariants}
              style={{
                position: 'fixed',
                top: 0,
                right: 0,
                height: '100vh',
                width: '240px',
                zIndex: 1300
              }}
            >
              <Drawer
                anchor="right"
                open={mobileOpen}
                onClose={handleDrawerToggle}
                PaperProps={{ 
                  sx: { 
                    width: 240,
                    backgroundColor: 'primary.main', // Fixed: theme.palette.primary.main se direct primary.main
                    color: 'white'
                  } 
                }}
              >
                <Box sx={{ pt: 8, px: 3 }}>
                  <List>
                    {['home', 'about', 'contact'].map((section, index) => (
                      <motion.div
                        key={section}
                        custom={index}
                        variants={listItemVariants}
                        initial="closed"
                        animate="open"
                      >
                        <ListItemButton
                          selected={isActive(section)}
                          onClick={() => {
                            scrollToSection(section);
                            setMobileOpen(false);
                          }}
                          sx={{
                            borderRadius: '8px',
                            mb: 1,
                            position: 'relative',
                            overflow: 'hidden',
                            '&.Mui-selected': {
                              backgroundColor: 'rgba(255, 255, 255, 0.2)',
                              fontWeight: 600
                            },
                            '&:hover': {
                              backgroundColor: 'rgba(255, 255, 255, 0.15)',
                            }
                          }}
                        >
                          {/* Slide-in animation for selected state */}
                          {isActive(section) && (
                            <motion.div
                              layoutId="mobileActiveIndicator"
                              style={{
                                position: 'absolute',
                                left: 0,
                                top: 0,
                                bottom: 0,
                                width: '4px',
                                backgroundColor: 'white',
                                borderRadius: '0 2px 2px 0'
                              }}
                              transition={{ type: "spring", stiffness: 300, damping: 30 }}
                            />
                          )}
                          
                          <ListItemText 
                            primary={section.charAt(0).toUpperCase() + section.slice(1)} 
                            sx={{
                              '& .MuiListItemText-primary': {
                                fontWeight: isActive(section) ? 600 : 400
                              }
                            }}
                          />
                          
                          {/* Arrow indicator */}
                          <motion.div
                            animate={{ x: isActive(section) ? 5 : 0 }}
                            transition={{ type: "spring", stiffness: 300 }}
                          >
                            <Box
                              component="span"
                              sx={{
                                width: '8px',
                                height: '8px',
                                borderRight: '2px solid white',
                                borderBottom: '2px solid white',
                                transform: 'rotate(-45deg)',
                                opacity: isActive(section) ? 1 : 0.5
                              }}
                            />
                          </motion.div>
                        </ListItemButton>
                      </motion.div>
                    ))}
                  </List>
                </Box>
              </Drawer>
            </motion.div>
          </Box>
        </Toolbar>
        
        {/* Progress indicator bar - Yeh sirf scrolled state ke liye */}
        <motion.div
          initial={{ scaleX: 0 }}
          animate={{ 
            scaleX: scrolled ? 1 : 0
          }}
          transition={{ duration: 0.3 }}
          style={{
            position: 'absolute',
            bottom: 0,
            left: 0,
            right: 0,
            height: '2px',
            backgroundColor: 'white',
            transformOrigin: 'left',
            opacity: 0.7
          }}
        />
      </Box>
    </motion.div>
  );
};

export default Header;